@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@700&display=swap);
body {

  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-main {
  background: -webkit-linear-gradient(110deg, rgb(174, 107, 255) 22%, rgba(165, 255, 247, 0) 22%), -webkit-linear-gradient(110deg, #FCFA51 77%, #ffc608 77%);
  min-height: 93vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.head {background-color: #e5e5f7;
    opacity: 0.8;
    background-image:  radial-gradient(#a86aff 1.1500000000000001px, transparent 1.1500000000000001px), radial-gradient(#a86aff 1.1500000000000001px, #e5e5f7 1.1500000000000001px);
    background-size: 46px 46px;
    background-position: 0 0,23px 23px;}

